@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/accordeon";
@import "components/howToMakeOrder";
@import "components/addres";
@import "components/deliveryPopup";
@import "components/fixed_nav";
@import "components/authorization";
@import "components/aside_buttons";

body {
  .header{
    position: unset!important;
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb breadcrumb"
                           "aside catalog popProd"
                           "order order order";
      grid-template-columns: 25% 60% 15%;
      .content__catalog__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__catalog {
        grid-area: catalog;
        &__medicalDevices {
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          div {
            h6 {
              margin: 25px 0;
              font: 1.125em/1.25em @sansregular;
              color: #252525;
              i {
                color: @fontColor;
              }
              &:hover {
                cursor: pointer;
              }
            }
            ul {
              li {
                margin-left: 50px;
              }
            }
          }
        }
        &__medicalDevices__info {
          padding-top: 115px;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          p {
            margin: 20px 0;
            padding: 0 150px 0 0;
            font: 1.125em/1.25em @sansregular;
            color: #252525;
          }
        }
      }
      .content__popularProducts {
        grid-area: popProd;
        padding-top: 150px;
        a {
          display: block;
          font: 1em/1.125em @sansbold;
          color: @linkColor;
          text-align: center;
          text-decoration: none;
        }
        ul {
          li {
            width: 250px;
            height: 350px;
            margin: 25px 0;
            position: relative;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            img {
              margin-top: 15px;
              width: 200px;
              height: 200px;
            }
            p {
              padding: 10px;
              font: 1em/1.125em @sansregular;
              color: @linkColor;
              text-align: left;
            }
            .price {
              font: 1.25em/1.375em @sansbold;
              span {
                font: .875em/1em @sansregular
              }
            }
            .hover{
              padding: 0;
              margin: 0;
              width: 100%;
              height: 100%;
              display: none;
              position: absolute;
              top: 0;
              z-index: 2;
              border: 1px solid #77c9cc;
              border-radius: 5px;
              box-shadow: 5px 5px 10px #e6e6e6;
              .link{
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(119, 201, 204, .3);
              }
              .addBusket,
              .addFavorite{
                margin: 10px 10px 0;
                display: block;
                width: 35px;
                height: 35px;
                position: relative;
                font: .875em/35px @sansregular;
                border-radius: 5px;
                z-index: 1;
              }
              .addBusket{
                color: #fff;
                background-color: @mainColor;
              }
              .addFavorite{
                color: @linkColor;
                background-color: #fff;
                border: 1px solid @linkColor;
                box-sizing: border-box;
              }
            }
            &:hover {
              border-color: @fontColor;
              cursor: pointer;
              transition: .5s;
              .hover{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-end;
                cursor: default;
              }
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
      }
    }
  }
}